function App() {
  const dispatch = useDispatch()
  useEffect(() => {
    basketData.map((el) => dispatch(addToBasketAction(el)))
  }, [])

  const [productsList, setProductsList] = useState("");

  useEffect(() => {
    const API = "https://fakestoreapi.com/products?limit=999";
    fetch(API)
      .then((response) => {
        if (response.ok) return response;
        else throw Error(response.statusText);
      })
      .then((response) => response.json())
      .then((response) => setProductsList(response))
      .catch((errors) => console.log(errors));
  }, []);
  return (

    <ThemeProvider theme={theme}>
      <Router basename={process.env.PUBLIC_URL}>
        <div className="App">
          <Switch>
            <Route path='/' exact render={() => <ProductsList productsList={productsList} />} />
            <Route path='/product/:id' render={(propsRoute) =>
              <ProductCard propsRoute={propsRoute} />}
            />
            <Route path='/checkout' component={Checkout} />
            <Route path='/basket' render={(propsRoute) =>
              <Basket propsRoute={propsRoute} />}
            />
            <Route render={() => (<div style={{ textAlign: 'center' }}>Page not found</div>)} />
          </Switch>
        </div>
      </Router>
    </ThemeProvider>
  );
}

export default App;
